import React, { useState, useEffect, useCallback } from "react";
import Header from "../../shared/components/Navigation/Header";
import Property from "../components/Property";
import { useForm } from "../../shared/hooks/form-hook";
import Input from "../../shared/components/UI/Input/Input";
import properties from "../../shared/seed-data";
import SideDrawer from "../../shared/components/UI/SideDrawer/SideDrawer";
import Footer from "../../shared/components/Footer/Footer";

import "./Properties.scss";

const Properties = props => {
  const [formState, inputHandler] = useForm({
    rooms: {
      value: "all"
    },
    bathrooms: "all",
    price: {
      value: "600000"
    },
    minSize: {
      value: "0"
    },
    maxSize: {
      value: "1000"
    }
  });

  const [filteredProperties, setFilteredProperties] = useState(properties);
  const [showNav, setShowNav] = useState(false);
  const { rooms, bathrooms, price, minSize, maxSize } = formState.inputs;

  const toggleNav = () => {
    setShowNav(prevState => !prevState);
  };

  const filterProperties = useCallback(
    properties => {
      let modifiedRooms = rooms.value === "all" ? rooms.value : +rooms.value;
      let modifiedBathrooms =
        bathrooms.value === "all" ? bathrooms.value : +bathrooms.value;
      let modifiedPrice = +price.value;
      let modifiedMinSize = +minSize.value;
      let modifiedMaxSize = +maxSize.value;

      return properties.filter(property => {
        return (
          +property.price <= modifiedPrice &&
          property.space >= modifiedMinSize &&
          property.space <= modifiedMaxSize &&
          (modifiedBathrooms === "all"
            ? true
            : +modifiedBathrooms === 3
            ? +property.bathrooms >= modifiedBathrooms
            : +property.bathrooms === modifiedBathrooms) &&
          (modifiedRooms === "all"
            ? true
            : +modifiedRooms === 5
            ? +property.rooms >= modifiedRooms
            : +property.rooms === modifiedRooms)
        );
      });
    },
    [rooms, bathrooms, price, minSize, maxSize]
  );

  useEffect(() => {
    const displayProperties = filterProperties(properties);
    // setFilteredProperties(() => {
    //   return displayProperties;
    // });
    setFilteredProperties(displayProperties);
  }, [filterProperties, rooms, bathrooms, price, minSize, maxSize]);
  return (
    <React.Fragment>
      <div className="properties">
        <SideDrawer show={showNav} />
        <Header isProperties show={showNav} onToggle={toggleNav} />
        <div className="form-group">
          <h2 className="heading-2 mb-lg">Search Houses</h2>
          <form className="form-group__filter-form">
            <div className="select">
              <Input
                id="rooms"
                onInput={inputHandler}
                element="select"
                label="Rooms"
                initialValue={formState.inputs.rooms.value}
              >
                <option value="all">all</option>
                <option value="1">One</option>
                <option value="2">Two</option>
                <option value="3">Three</option>
                <option value="4">Four</option>
                <option value="5">Five plus</option>
              </Input>
            </div>
            <div className="select">
              <Input
                id="bathrooms"
                onInput={inputHandler}
                element="select"
                label="Bathrooms"
                initialValue={formState.inputs.rooms.value}
              >
                <option value="all">all</option>
                <option value="1">One</option>
                <option value="2">Two</option>
                <option value="3">Three plus</option>
              </Input>
            </div>
            <div className="price">
              <Input
                id="price"
                type="range"
                min={350000}
                max={2000000}
                label="House Price € "
                onInput={inputHandler}
                initialValue={formState.inputs.price.value}
                step={100}
              />
            </div>
            <div className="input__size">
              <Input
                id="minSize"
                type="number"
                label="House Size "
                onInput={inputHandler}
                initialValue={formState.inputs.minSize.value}
                min={0}
                max={1000}
              />
              <Input
                id="maxSize"
                type="number"
                onInput={inputHandler}
                initialValue={formState.inputs.maxSize.value}
                min={0}
                max={1000}
              />
            </div>
          </form>
        </div>
        <Property properties={filteredProperties} />
      </div>
      <Footer />
    </React.Fragment>
  );
};

export default Properties;
