import React, { useState, useEffect, useCallback } from "react";
import { useParams } from "react-router-dom";

import Header from "../../shared/components/Navigation/Header";
import SideDrawer from "../../shared/components/UI/SideDrawer/SideDrawer";
import properties from "../../shared/seed-data";
import Slider from "react-slick";
import InfoTabs from '../../shared/components/UI/Tabs/Tabs';
import Footer from '../../shared/components/Footer/Footer';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import "./PropertyDetails.scss";

const PropertyDetails = props => {
  const [property, setProperty] = useState();
  const [showNav, setShowNav] = useState(false);

  let source;
  if (property) {
    source = property[0].galleryPicts.map(img => {
      return img;
    });
  }

  const settings = {
    customPaging: i => {
      return <img src={source[i].imageUrl} alt={source[i].desc} />;
    },
    dots: true,
    dotsClass: "slick-dots slick-thumb",
    arrows: false,
    infinite: true,
    speed: 2000,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3800
  };

  const { id } = useParams();
  const toggleNav = () => {
    setShowNav(prevState => !prevState);
  };

  const getProperty = useCallback(
    properties => {
      return properties.filter(property => property.id === +id);
    },
    [id]
  );

  useEffect(() => {
    const displayedProperty = getProperty(properties);
    setProperty(displayedProperty);
  }, [getProperty]);

  let slider = <p>Loading...</p>;
  if (property) {
    slider = (
      <Slider {...settings}>
        {property[0].galleryPicts.map(img => {
          return (
            <figure key={img.desc} className="room-item">
              <img src={img.imageUrl} alt={img.desc} />
            </figure>
          );
        })}
      </Slider>
    );
  }

  return (
    <React.Fragment>
    <div className="property-details">
      <SideDrawer show={showNav} />
      <Header isProperties show={showNav} onToggle={toggleNav} />
      <section className="property-details__gallery mb-lg">{slider}</section>
      <section className="property-details__info">
       <InfoTabs property={property}/>
      </section>
    </div>
    <Footer />
    </React.Fragment>
  );
};

export default PropertyDetails;
