import React, { useReducer, useEffect } from "react";

import modifyPrice from '../../../../shared/util';

import "./Input.scss";

const inputReducer = (state, action) => {
  switch (action.type) {
    case "CHANGE":
      return {
        ...state,
        value: action.val
      };
    default:
      return state;
  }
};

const Input = props => {
  const [inputState, dispatch] = useReducer(inputReducer, {
    value: props.initialValue || ""
  });

  const { id, onInput } = props;
  const { value } = inputState;

  useEffect(() => {
    onInput(id, value);
  }, [id, value, onInput]);

  const changeHandler = event => {
    dispatch({
      type: "CHANGE",
      val: event.target.value
    });
  };

  let element;
  switch (props.element) {
    case "input":
      element = (
        <input
          id={props.id}
          type={props.type}
          placeholder={props.placeholder}
          onChange={changeHandler}
          value={inputState.value}
          min={props.min}
          max={props.max}
          step={props.step}
        />
      );
      break;
    case "textarea":
      element = (
        <textarea
          id={props.id}
          rows={props.rows || 3}
          onChange={changeHandler}
          value={inputState.value}
        />
      );
      break;
    case "select":
      element = (
        <select id={props.id} onChange={changeHandler}>
          {props.children}
        </select>
      );
      break;
    default:
      element = (
        <input
          id={props.id}
          type={props.type}
          placeholder={props.placeholder}
          onChange={changeHandler}
          value={inputState.value}
          min={props.min}
          max={props.max}
          step={props.step}
        />
      );
      break;
  }
  return (
    <div>
      <label htmlFor={props.id}>
        {props.label}
        {props.id ==='minSize' && <React.Fragment>m<sup>2</sup></React.Fragment>}
        {props.id === 'price' && modifyPrice(inputState.value)}
      </label>
      {element}
    </div>
  );
};

export default Input;
