import React from "react";
import SvgIcon from "@material-ui/core/SvgIcon";
import {
  FavoriteOutlined,
  LocationOnOutlined,
  SingleBedOutlined,
  SquareFootOutlined,
  EuroOutlined
} from "@material-ui/icons";
import { useHistory } from "react-router-dom";

import modifyPrice from "../../util";
import Button from "../UI/Button/Button";

const HouseCard = props => {
  const history = useHistory();
  return (
    <div className="house">
      <img
        className={`house__img`}
        src={props.imageUrl}
        alt={`${props.index + 1}`}
      />
      <div className="house__like">
        <SvgIcon>
          <FavoriteOutlined />
        </SvgIcon>
      </div>
      <h5 className="house__name">{props.title}</h5>
      <div className="house__location">
        <SvgIcon>
          <LocationOnOutlined />
        </SvgIcon>
        <p>{props.country}</p>
      </div>
      <div className="house__rooms">
        <SvgIcon>
          <SingleBedOutlined />
        </SvgIcon>
        <p>{props.rooms}</p>
      </div>
      <div className="house__area">
        <SvgIcon>
          <SquareFootOutlined />
        </SvgIcon>
        <p>
          {props.space} m<sup>2</sup>
        </p>
      </div>
      <div className="house__price">
        <SvgIcon>
          <EuroOutlined />
        </SvgIcon>
        <p>€{modifyPrice(props.price)}</p>
      </div>
      <Button
        class="house__btn"
        to={props.viewMore && history.location.pathname + "/" + props.id}
      >
        {props.viewMore ? "View More" : "Contact agent"}
      </Button>
    </div>
  );
};

export default HouseCard;
