import React from "react";

import {
  makeStyles,
  createMuiTheme,
  ThemeProvider
} from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "../Button/Button";
import modifyPrice from "../../../util";
import "./Tabs.scss";

const theme1 = createMuiTheme({
  palette: {
    primary: {
      main: "#c69963"
    },
    secondary: {
      main: "#101d2c"
    }
  },
  typography: {
    fontfamily: "Lato",
    fontSize: 30
  }
});

const theme2 = createMuiTheme({
  typography: {
    fontFamily: "Lato",
    fontSize: 25
  }
});

const TabPanel = props => {
  const { children, value, index, ...other } = props;
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
};

const a11yProps = index => {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`
  };
};

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper
  }
}));

const InfoTab = props => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  let property;
  if (props.property) {
    property = props.property[0];
  }
  
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <ThemeProvider theme={theme1}>
          <Tabs
            className="tabs"
            value={value}
            onChange={handleChange}
            aria-label="simple tabs example"
          >
            <Tab label="Description" {...a11yProps(0)} />
            <Tab label="Info" {...a11yProps(1)} />
            <Tab label="Extra" {...a11yProps(2)} />
          </Tabs>
        </ThemeProvider>
      </AppBar>
      <ThemeProvider theme={theme2}>
        <TabPanel className="panel-padding" value={value} index={0}>
          <h2 className="heading-2 mb-md">Description</h2>
          <p>
            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Impedit
            rerum eum officia pariatur asperiores earum quisquam eaque, modi
            iusto libero corrupti officiis id voluptates itaque quas ipsam aut
            reiciendis esse.
          </p>
        </TabPanel>
        <TabPanel className="panel-padding" value={value} index={1}>
          <h2 className="heading-2 mb-md">Details</h2>
          {property && (
            <div className="tabs__info--box">
              <div className="tabs__info--box-summary">
                <p>
                  Lorem ipsum dolor sit amet consectetur, adipisicing elit.
                  Eveniet labore iure cum recusandae magni nobis delectus sint,
                  voluptas impedit id culpa necessitatibus quod natus nostrum
                  nesciunt ratione reiciendis modi quam!
                </p>
              </div>
              <div className="tabs__info--box-details">
                <p>Price: €{modifyPrice(property.price)}</p>
                <p>Rooms: {property.rooms}</p>
                <p>Bathrooms: {property.bathrooms}</p>
                <p>
                  Space: {property.space}m<sup>2</sup>
                </p>
              </div>
              <Button to="/">Contact a Realtor</Button>
            </div>
          )}
        </TabPanel>
        <TabPanel className="panel-padding" value={value} index={2}>
          <h2 className="heading-2 mb-md">Extra Info</h2>
          <p>
            Lorem ipsum dolor sit, amet consectetur adipisicing elit. Illo
            sapiente et quod maxime assumenda ipsum a vero ad magni, tenetur
            iure expedita consequuntur.
          </p>
        </TabPanel>
      </ThemeProvider>
    </div>
  );
};

export default InfoTab;
